    //-------------------录屏-------------------------------------------
    /**
     * 配置录屏
     */
    
    private static ryw_initRecord(){
        ryw_TTAPI.ryw_record = Laya.Browser.window["tt"].getGameRecorderManager();  
        if(ryw_TTAPI.ryw_record!=null){
            ryw_TTAPI.ryw_record.onStart(res =>{
                console.log("录屏开始");
                ryw_TTAPI.ryw_recordRes="";        
            })

            ryw_TTAPI.ryw_record.onStop(res =>{
                console.log("录屏结束");
                ryw_TTAPI.ryw_recordRes = res.videoPath;
            })
        }
    }

    /**
     * 开始录屏
     */
    public static ryw_startRecord(duration = 300){
        if(!ryw_AppConfig.onTTMiniGame)return;
        ryw_TTAPI.ryw_record.start({
            duration
        })
    }

     /**
     * 停止录屏
     */
    public static ryw_stopRecord() {
        if(!ryw_AppConfig.onTTMiniGame)return;                     
        ryw_TTAPI.ryw_record.stop();
    }

    //----------------------------------------------------------------------

    //---------------------分享录屏----------------------------------------
    public static ryw_shareRecord(callback: Function = null, Failcallback: Function = null) {
        if (!ryw_AppConfig.onTTMiniGame) return;
        if (ryw_TTAPI.ryw_recordRes != "") {
            window["tt"].shareAppMessage({
                channel: "video",
                extra: {
                    videoPath: ryw_TTAPI.ryw_recordRes, // 可替换成录屏得到的视频地址
                    videoTopics: [ryw_AppConfig.ryw_GameName]
                },
                success() {
                    if (callback != null) {
                        callback();
                    }
                    console.log("分享视频成功");
                },
                fail(e) {
                    console.log("分享视频失败");
                    if (Failcallback != null) {
                        Failcallback();
                    }
                }
            });
        } else {
            if (Failcallback != null) {
                Failcallback();
            }
            console.log("分享视频为空");
        }
    }
    //----------------------------------------------------------------------


    //----------------------------------------------------------------------

    //---------------------分享好友----------------------------------------
    public static ryw_share(complate:Function=null){
        if(!ryw_AppConfig.onTTMiniGame)return;   
        window["tt"].shareAppMessage({
            templateId:ryw_TTAPI.ryw__templateId,
            success() {
                if(complate!=null){
                    complate();
                }
            },
            fail() {
                console.log("分享失败");
            }
            });

    }

