    public static ryw_showMoreGamesModal(onSuccess : Function,onFail : Function)
    {
        const systemInfo = Laya.Browser.window["tt"].getSystemInfoSync();
        // iOS 不支持，建议先检测再使用
        if (systemInfo.platform !== "ios") {
            // 打开互跳弹窗
            Laya.Browser.window["tt"].showMoreGamesModal({
                appLaunchOptions: [
                    {
                        appId: ryw_AppConfig.ryw_AppID,
                        query: "foo=bar&baz=qux",
                        extraData: {}
                    }
                    // {...}
                ],
                success(res) {
                    console.log("success", res.errMsg);
                    if(onSuccess)
                    {
                        onSuccess();
                    }
                },
                fail(res) {
                    console.log("fail", res.errMsg);
                    if(onFail)
                    {
                        onFail();
                    }
                }
            });
        }
        else
        {
            if(onFail)
            {
                onFail();
            }
        }
    }

    //自动弹出签到界面，如果可以签到则弹出签到界面并回调参数 true，如果不可以签到则不弹出签到界面并回调参数 false
    public static autoOpenSignInView(complate : Function)
    {
        ryw_HttpUnit.GetSignIn((res)=>
        {
            let isSign : number = res.data.is_sign;
            let signDays : number = res.data.sign_day_num;
            if(isSign == 0)
            {
                ryw_ViewMgr.ryw_instance.ryw_openView(ryw_ViewDef.ryw_TTSignInView,null,()=>
                {
                    complate(true);
                })
            }
            else
            {
                complate(false);
            }
        },()=>
        {
            complate(false);
        })
    }

